
<style scoped>

</style>


<template>
    <div>
        <el-card>
            <el-alert
                title='添加商品信息'
                show-icon
                :closable=false
                type='warning'
            />
            <el-row>
                <el-col :span="24">
                    <span >选择商品分类：</span>
                    <el-cascader
                        :options='data_on_ready'
                        :props="{ 
                            expandTrigger: 'hover',
                            label:'cat_name',
                            value:'cat_id'
                        }"
                        @change='cascader_change'
                    ></el-cascader>
                </el-col>
            </el-row>
            <el-tabs 
                v-model="tab_active"
                @tab-click='tabs_change'
            >
                <el-tab-pane label='动态参数' name='many' >
                    <el-table
                        :data='data_on_cat_certain'
                    >
                        <el-table-column type='expand' >
                            <template slot-scope="scoped">
                                <el-tag 
                                    v-for='(m,i) in scoped.row.attr_vals.split(",")' :key='i'
                                >
                                    {{ m }}
                                </el-tag>
                                <el-input
                                    class="input-new-tag"
                                    v-if="inputVisible"
                                    v-model="inputValue"
                                    ref="saveTagInput"
                                    size="small"
                                    @keyup.enter.native="handleInputConfirm(scoped.row)"
                                />
                                <el-button 
                                    class="button-new-tag" 
                                    size="small" 
                                    v-else
                                    @click="showInput"
                                >+ New Tag</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column prop='attr_id' label='#' />
                        <el-table-column prop='attr_name' label='参数名称' />
                        <el-table-column prop='option' label="操作" >
                            <div slot-scope="scoped">
                                <el-button type='primary' icon="el-icon-edit" @click="run_editor(scoped.row,'edit')"></el-button>
                                <el-button type='danger' icon='el-icon-delete' @click="run_editor(scoped.row,'del')"></el-button>
                            </div>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label='静态属性' name='only'>
                    <el-table
                        :data='data_on_cat_certain'
                    >
                        <el-table-column type='expand' >
                            <template slot-scope="scoped">
                                <div>
                                <el-tag 
                                    v-for='(m,i) in scoped.row.attr_vals.split(",")' :key='i'
                                >
                                    {{ m }}
                                </el-tag>
                                <el-input
                                    class="input-new-tag"
                                    v-if="inputVisible"
                                    v-model="inputValue"
                                    ref="saveTagInput"
                                    size="small"
                                    @keyup.enter.native="handleInputConfirm(scoped.row)"
                                />
                                <el-button 
                                    class="button-new-tag" 
                                    size="small" 
                                    v-else
                                    @click="showInput"
                                >+ New Tag</el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop='attr_id' label='#' />
                        <el-table-column prop='attr_name' label='属性名称' />
                        <el-table-column prop='option' label="操作" >
                            <div slot-scope="scoped">
                                <el-button type='primary' icon="el-icon-edit" @click="run_editor(scoped.row,'edit')"></el-button>
                                <el-button type='danger' icon='el-icon-delete' @click="run_editor(scoped.row,'del')"></el-button>
                            </div>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <el-dialog
            title="修改参数"
            :visible='click_edit_btn'
        >
            <div>
                <el-input v-model="val_in_mask_of_edit">
                    <div slot="prepend">参数：</div>
                </el-input>
            </div>
            <div slot="footer">
                <el-button @click="click_edit_btn=false">取消</el-button>
                <el-button type='primary' @click="edit_sure(r)">确定</el-button>
            </div>

        </el-dialog>
        <el-dialog
            :visible='click_del_btn'
        >
            <div slot="footer">
                <el-button @click="click_del_btn=false">取消</el-button>
                <el-button type='primary'  @click="del_sure(r)">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>


<script>
export default {
    name:'sortarg',
    data() {
        return {
            // 预备数据
            data_on_ready:null,
            // 选项卡 活跃项
            tab_active:'many',
            // 编辑
            click_edit_btn:false,
            // 删除
            click_del_btn:false,
            // 条件检索数据
            data_on_cat_certain:null,
            // 表格行对象
            r:null,
            // 记录级联选项
            cat_confirmed:-1,
            // 新添标签
            inputVisible:false,
            inputValue:'',
            // 修改参数
            val_in_mask_of_edit:'',
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        async oninit(){
            let {data:res}=await this.$http.get(
                `/categories`,{params:{
                    type:3
                }}
            )
            if(res.data){
                this.data_on_ready=res.data
                console.log('获取分类参数',res)
            }else{
                this.$message(res.meta.msg)
            }
        },
        async cascader_change(node_value){
            console.log(123)
            console.log(node_value)
            this.cat_confirmed=node_value[2]
            let {data:res}=await this.$http.get(
                `categories/${this.cat_confirmed}/attributes`,{params:{
                    sel:this.tab_active
                }}
            )

            console.log(res)
            this.data_on_cat_certain=res.data?res.data:null
            console.log( '获取参数列表',this.data_on_cat_certain)
        },
        async tabs_change(o){
            console.log(o)
            this.tab_active=(o.index-0)?'only':'many'
            if(this.cat_confirmed!=-1){
                let {data:res}=await this.$http.get(
                    `categories/${this.cat_confirmed}/attributes`,{
                        params:{
                        sel:this.tab_active
                    }
                })
                console.log(res.data)
                this.data_on_cat_certain=res.data
            }else{
                this.data_on_cat_certain=null
            }
        },
        
        run_editor(r,str){
            this.r=r
            switch(str){
                case 'edit':
                    this.val_in_mask_of_edit=this.r.attr_name
                    this.click_edit_btn=true
                break;
                case 'del':
                    this.click_del_btn=true
                break;
            }
        },
        async edit_sure(r){
            let {data:res}=await this.$http.put(
                `/categories/${r.cat_id}/attributes/${r.attr_id}`,{
                    attr_sel:this.tab_active,
                    attr_name:this.val_in_mask_of_edit,
                    attr_vals:r.attr_vals
                }
            )
            console.log('修改回执',res)
            this.click_edit_btn=false
            this.r=null
            this.cascader_change([0,0,this.cat_confirmed])

        },
        async del_sure(r){
            let {data:res}=await this.$http.delete(
                `/categories/${r.cat_id}/attributes/${r.attr_id}`,{}
            )
            console.log('删除回执',res)
            this.click_del_btn=false
            this.r=null
            this.cascader_change([0,0,this.cat_confirmed])

        },

        // 填入新标签
        showInput(){
            
            this.inputVisible = true;
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        handleInputConfirm(r){
            
            console.log(r)
            let v = this.inputValue.trim();
            if(!v){
                this.$message('不可为空')
            }else{
                let attr_vals=r.attr_vals.split(',')
                    attr_vals.push(v)
                    attr_vals=attr_vals.join(',')
                let url=`/categories/${r.cat_id}/attributes/${r.attr_id}`
                let args={
                    attr_name:r.attr_name,
                    attr_sel:this.tab_active,
                    attr_vals
                }
                console.log(url,args)
                this.add_new_tag(url,args)
            }
            this.inputVisible = false;
            this.inputValue = '';
        },
        async add_new_tag(str,args){
            let {data:res}=await this.$http.put(str,args)
            console.log(res.data)
            this.cascader_change([0,0,this.cat_confirmed])
        }
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>